.statistics {
  text-align: center;
  border-radius: 15px 15px 0 0;
  box-shadow: 0 1px 3px rgb(0 0 0/12%), 0 1px 1px rgb(0 0 0/14%),
    0 2px 1px rgb(0 0 0/20%);
  width: 400px;
  margin-left: auto;
  margin-right: auto;
}

.title {
  font-size: 30px;
  font-weight: bold;
  padding-top: 20px;
  padding-bottom: 20px;
  background-color: rgb(255, 255, 255);
  color: rgb(76, 75, 75);
  border-radius: 15px 15px 0 0;
  /* width: 500px;
     margin-left: auto;
    margin-right: auto; */
}

.stat_list {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
}

.item {
  display: flex;
  flex-direction: column;
  outline: 1px solid darkgrey;
  padding-top: 5px;
  padding-bottom: 5px;
}

.label {
  color: white;
  font-size: 14px;
}

.percentage {
  color: white;
  font-size: 20px;
}
